import React from 'react';
import './style.css';

const Teoria = () => {
    return (
        <div className="teoria-container">
            <h1 className="text-center">Teoría del Movimiento de un Helicóptero</h1>
            <p className="lead">
                Para comprender el movimiento de un helicóptero, es importante conocer las ecuaciones que describen su movimiento longitudinal y de balanceo. Estas ecuaciones son fundamentales para entender cómo el helicóptero se desplaza y se equilibra en el aire.
            </p>
            <h2 className="text-left">Ecuaciones del Movimiento Longitudinal</h2>
            <p>
                El movimiento longitudinal de un helicóptero se describe mediante la siguiente ecuación diferencial:
                <br />
                <strong>m (d²x/dt²) = Fp - ce (dx/dt)²</strong>
                <br />
                Donde:
                <br />
                - <em>m</em> es la masa del helicóptero.
                <br />
                - <em>Fp</em> es la fuerza del propulsor.
                <br />
                - <em>ce</em> es el coeficiente de resistencia.
            </p>
            <h2 className="text-left">Ecuaciones del Movimiento de Balanceo</h2>
            <p>
                El movimiento de balanceo de un helicóptero se describe mediante la siguiente ecuación diferencial:
                <br />
                <strong>Ix (d²θ/dt²) = d (Fu - Fb) - ct (dθ/dt)</strong>
                <br />
                Donde:
                <br />
                - <em>Ix</em> es el momento de inercia.
                <br />
                - <em>d</em> es la distancia del centro de masas al eje de balanceo.
                <br />
                - <em>ct</em> es el coeficiente de torsión.
                <br />
                - <em>Fu</em> es la fuerza de sustentación.
                <br />
                - <em>Fb</em> es la fuerza de balanceo.
            </p>
            <h2 className="text-center">Video explicativo ecuaciones diferenciales del movimiento</h2>
            <div className="d-flex justify-content-center mb-4">
                <iframe 
                    title="Video de YouTube"
                    width="560" 
                    height="315" 
                    src="https://www.youtube.com/embed/xbGj5iuAJHw" 
                    frameBorder="0" 
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                    allowFullScreen
                    style={{ maxWidth: '100%' }}
                ></iframe>
            </div>
            <h2 className="text-center">Artículos de Interés</h2>
            <ul>
                <li>
                    <strong>Movimiento longitudinal: Dinámica de un vehículo en una pendiente</strong>
                    <p>Este artículo explora la dinámica de un vehículo que se mueve en una pendiente, considerando factores como la resistencia del aire, la fricción y la inclinación de la pendiente. Se utilizan ecuaciones diferenciales para modelar el movimiento del vehículo y se analizan las condiciones para que el vehículo suba, baje o se mantenga en reposo.</p>
                    <a href="https://www.researchgate.net/publication/277977378_Modelling_and_validation_of_the_vehicle_longitudinal_model" target="_blank" rel="noopener noreferrer">Leer más</a>
                </li>
                <li>
                    <strong>Movimiento de cabeceo: Análisis de la estabilidad de un avión</strong>
                    <p>Este artículo analiza la estabilidad del movimiento de cabeceo de un avión, es decir, su capacidad para mantener una actitud horizontal durante el vuelo. Se utilizan ecuaciones diferenciales para modelar el movimiento del avión y se estudian los factores que influyen en su estabilidad, como la distribución de masas, la aerodinámica y el piloto automático.</p>
                    <a href="https://www.researchgate.net/figura/Lift-drag-ratio-as-a-function-of-lift-coefficient-estimated-by-aerodynamic-analysis-and_fig7_262185433" target="_blank" rel="noopener noreferrer">Leer más</a>
                </li>
                <li>
                    <strong>Runge-Kutta: Un método numérico para resolver ecuaciones diferenciales</strong>
                    <p>Este artículo presenta el método de Runge-Kutta, un método numérico ampliamente utilizado para resolver ecuaciones diferenciales ordinarias. Se explica el algoritmo de Runge-Kutta en detalle y se muestran ejemplos de su aplicación.</p>
                    <a href="https://support.khanacademy.org/hc/en-us/community/posts/15295294509069-Numerical-Analysis-Methods-for-solving-partial-differential-equations" target="_blank" rel="noopener noreferrer">Leer más</a> | <a href="https://www.youtube.com/watch?v=-1aNhFO1TcQ" target="_blank" rel="noopener noreferrer">Ver video</a>
                </li>
            </ul>
        </div>
    );
}

export default Teoria;